
import listdata from './yes24Data.js';
export default {
    data() {
        return {
            episodes : [],
            episodesLength : 0,
            search: '',
            searchedData: [],
            sortBy: 'newest',
            sortTx : '최신회부터',
            sortCate: '전체',
            lastItemId: 0,
            selectedKeyword: '',
            filterActive: false,
            filteredList: [],
            filterClass: 'mdi-filter-outline',
            filterActiveClass: 'mdi-filter',
            viewBtnName : ['에피소드', '도서'],
            sortBtnName : ['전체', '측면돌파', '삼천포책방', '옹기종기', '어떤,책임','읽는인간','책책책', '이주의 도서'],
            targetIndex: 0,
            cardData : [],
            reviewer: '',
            listType: 'episode',
            searchOption: 'episodeTit',
            reviewerList : [],
        }

    },
    created() {
        this.episodes = listdata;
        this.episodesLength = listdata.length;
        this.lastItemId = this.episodes[this.episodes.length - 1].id;
    },
    computed: {
        filteredData() {
            this.filteredList = this.episodes;
            if (this.search != '' && this.search) {
                if(this.searchOption !== 'episodeTit'){
                    this.searchedData = this.filteredList.filter((data) => {
                        return data.books.some((book) => {
                            return book[this.searchOption].includes(this.search);
                        })
                    })
                    this.searchedData.forEach((a) => {
                        this.cardData = a.books.filter((b) => {
                            return b[this.searchOption].includes(this.search) 
                        })
                        a.books = this.cardData
                    })
                }else {
                    this.searchedData = this.filteredList.filter((data) => {
                        return data.episodeTit.includes(this.search);
                    })
                    
                }
                this.filteredList = this.searchedData;
            }

            // if (this.reviewer !== '' && this.reviewer) {
            //     this.reviewerList = this.filteredList.filter((data) => {
            //         return data.books.some((book) => {
            //             return book.name.includes(this.reviewer);
            //         })
            //     })
            //     this.reviewerList.forEach((a) => {
            //         this.cardData = a.books.filter((b) => {
            //             return b.name.includes(this.reviewer) 
            //         })
            //         a.books = this.cardData
            //     })
            //     this.reviewerList = this.filteredList
            // }

            
            this.filteredList = this.filteredList.sort((a, b) => {
                if(this.sortBy == 'oldest') {
                    return a.id -  b.id;  
                } else if (this.sortBy == 'newest') {
                    return b.id - a.id;
                }
            })

            if (this.sortCate != '전체') {
                this.filteredList = this.filteredList.filter(
                (data) => {
                    return data.category.includes(this.sortCate);
                });
            } 
                
            if (this.selectedKeyword != '' && this.selectedKeyword) {
                this.filteredList = this.filteredList.filter((data) => {
                    return data.books.some((book) => {
                        return book.tags.some((tag) => {
                            return tag.includes(this.selectedKeyword);
                        })
                    })
                })
                this.filteredList.forEach((a) => {
                    this.cardData = a.books.filter((b) => {
                        return b.tags.some((tag) => {
                            return tag.includes(this.selectedKeyword) 
                        })  
                    })
                    a.books = this.cardData
                })
            }

            if(this.filteredList.length !== this.episodesLength) {
                this.filterActive = true;
            }
            return this.filteredList
        },
    },
    methods: {
        sortCtrl() {
            if(this.sortBy == 'oldest') {
                this.sortBy = 'newest'
                this.sortTx = '최신회부터'
            }else {
                this.sortBy = 'oldest'
                this.sortTx = '첫회부터'
            }
        },
        accordion(event) {
            let el = event.currentTarget;
            let target = el.nextElementSibling;
            if(target.style.display == 'none') {
                target.style.display = 'block';
                el.querySelector('.mdi-menu-down').classList.replace('mdi-menu-down', 'mdi-menu-up');
            }else {
                target.style.display = 'none'
                el.querySelector('.mdi-menu-up').classList.replace('mdi-menu-up', 'mdi-menu-down');
            }
        }, 
        getKeyword(event){
            let el = event.currentTarget;
            let word = el.innerHTML;
            this.selectedKeyword = word;
        },

        // getReviwer(event){
        //     let el = event.currentTarget;
        //     let name = el.innerHTML;
        //     if(this.listType == 'episode') {
        //         this.reviewer = '';
        //     }else {
        //         this.reviewer = name;
        //     }
        // },
        setFilterActive() {
            this.sortCate = '전체';
            this.selectedKeyword = '';
            this.search = '';
            this.filteredList = this.episodes;
            this.filterActive = false;
            // this.reviewer = '';
            document.querySelector('.sort-category').children.forEach(a => {
                a.classList.remove('active')
            })
            document.querySelector('.sort-category').firstElementChild.classList.add('active');
            
        },
        sortCategory(event) {
            let el = event.currentTarget;
            let value;
            let text;
            
            el.parentElement.children.forEach(a => {
                a.classList.remove('active')
            })
            el.classList.add('active');

            if(el.id == 'selectCate') {
                value = el.value;
                if(value == 'sort0') {
                    text = '전체'
                }else if(value == 'sort1') {
                    text = '측면돌파'
                }else if (value == 'sort2') {
                    text = '삼천포책방'
                }else if (value == 'sort3') {
                    text = '옹기종기'
                }else if (value == 'sort4') {
                    text = '어떤,책임'
                }else if (value == 'sort5') {
                    text = '읽는인간'
                }else if (value == 'sort6') {
                    text = '책책책'
                }
            }else {
                text = el.innerHTML;
            }
            
            // this.reviewer = '';
            this.sortCate = text;
        },

        setListType(event) {
            let el = event.currentTarget;
            let type = el.getAttribute('data-type')
            this.listType = type;
        },

        setSearchOption(event){
            let el = event.currentTarget;
            let value = el.value
            this.searchOption = value
        }
        
    }

}